<template>
  <div>

    <!-- element UI 和 IView 这两个UI组件库处理表单，绑定数据是什么，表单就展示什么，数据变化，表单就变化，表单变化，数据就变化  -->
    <!-- ant design中，表单处理方式不一样，绑定数据是什么，就初始显示什么，数据变，表单不变，表单变，数据也不变。 -->
    <a-form layout="horizontal">
      <a-form-item
        label="用户名："
        :label-col="{ span: 4 }"
        :wrapper-col="{ span: 14 }"
        :validateStatus="usernameValidateStatus"
        :help="usernameHelp"
      >
        <a-input v-model="username" placeholder="input placeholder" />
      </a-form-item>
      <a-form-item
        label="密码："
        :label-col="{ span: 4 }"
        :wrapper-col="{ span: 14 }"
        :validateStatus="passwordValidateStatus"
        :help="passwordHelp"
      >
        <a-input v-model="password" placeholder="input placeholder" />
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 14, offset: 4 }">
        <a-button type="primary" @click="btnAction">Submit</a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      usernameValidateStatus: "",
      usernameHelp: "",
      passwordValidateStatus: "",
      passwordHelp: ""
    };
  },
  watch: {
    username(newVal) {
      if (newVal.length > 0) {
        this.usernameValidateStatus = "";
        this.usernameHelp = "";
      } else {
        this.usernameValidateStatus = "error";
        this.usernameHelp = "输入不能为空";
      }
    },
    password(newVal) {
      if (newVal.length >= 8) {
        this.passwordValidateStatus = "";
        this.passwordHelp = "";
      } else {
        this.passwordValidateStatus = "error";
        this.passwordHelp = "输入不能少于8个字符";
      }
    }
  },
  methods: {
    btnAction() {
      if (this.username.length > 0 && this.password.length >= 8) {
        console.log("提交");
      } else if (this.username.length === 0) {
        this.usernameValidateStatus = "error";
        this.usernameHelp = "输入不能为空";
      } else {
        this.passwordValidateStatus = "error";
        this.passwordHelp = "输入不能少于8个字符";
      }
    }
  }
};
</script>

